@import "_config_global.scss";

* {
  //.box-sizing(); .transition(@transition_time, @transition_ease);
  transition: all $transition_time $transition_ease;
  box-sizing: border-box;
}

body { background-color: $background-color; font-size: $base-font-size; }

//Page content, without admin
.l-page {
  &, & a {
    font-family: 'robotoslabregular', Sans-serif; color: $main-color; font-size: 1.4em;
  }
}

a { display: inline-block;text-decoration: none; }
p { margin-bottom: 1em;}

/**
 * Titles
 */

h1, h2, h3, h4, h5, h6 { color: $main-color; font-family: 'robotoslabregular', Helvetica, Sans-Serif; line-height: normal; }
h1 { font-size: 2.4em; }
h2 { font-size: 1.6em; font-family: 'robotoslabbold', Helvetica, Sans-Serif; }

/**
 *  Default Main content Header
 */
.ec-content-header {
  display: table; height: 444px; width: 100%; position: relative; background-color: $main-background-color;
  //.over { position: relative; z-index: 2; vertical-align: middle; display: table-cell; }
  .bkgd {
    position: absolute; z-index: 1; top: 0; left: 0; width: 100%; height: 100%; overflow: hidden;
    img {
      max-width: none; min-height: 100%; min-width:100%; height: auto; width: auto; margin: auto; display: block;
      position: absolute; top: -200%; bottom: -200%; left: -200%; right: -200%;
    }
  }
  /*.desc {
    margin-top: (@space - 5px);
    p { color: @third-color; font-size: 20px; font-family: robotos; text-align: center; }
  }*/
  //.bt1 { margin-top: (@space - 5px); }
}